import type { VercelRequest, VercelResponse } from '@vercel/node'
import jwt from 'jsonwebtoken'

export interface AuthRequest extends VercelRequest {
  user?: {
    id: string
    email: string
    role: 'admin' | 'user'
    clinicId?: string
    name: string
  }
}

// Usar vari√°vel de ambiente ou fallback
const JWT_SECRET = process.env.JWT_SECRET || 'your-secret-key-change-in-production'

if (!process.env.JWT_SECRET) {
  console.warn('‚ö†Ô∏è  JWT_SECRET n√£o configurado! Usando valor padr√£o inseguro.')
}

export function authenticateToken(
  req: AuthRequest,
  res: VercelResponse,
  next: () => void
) {
  const authHeader = req.headers['authorization']
  const token = authHeader && authHeader.split(' ')[1]

  if (!token) {
    return res.status(401).json({ error: 'Token n√£o fornecido' })
  }

  try {
    const decoded = jwt.verify(token, JWT_SECRET) as AuthRequest['user']
    req.user = decoded
    next()
  } catch (error) {
    return res.status(403).json({ error: 'Token inv√°lido ou expirado' })
  }
}

export function requireAdmin(
  req: AuthRequest,
  res: VercelResponse,
  next: () => void
) {
  if (req.user?.role !== 'admin') {
    return res.status(403).json({ error: 'Acesso negado. Admin necess√°rio.' })
  }
  next()
}

export function optionalAuth(
  req: AuthRequest,
  res: VercelResponse,
  next: () => void
) {
  const authHeader = req.headers['authorization']
  const token = authHeader && authHeader.split(' ')[1]

  if (token) {
    try {
      const decoded = jwt.verify(token, JWT_SECRET) as AuthRequest['user']
      req.user = decoded
    } catch (error) {
      // Token inv√°lido, mas continua sem autentica√ß√£o
    }
  }
  next()
}




