// Este arquivo s√≥ roda no servidor (Node.js)

import { Pool } from 'pg'

// Validar DATABASE_URL
if (!process.env.DATABASE_URL) {
  console.error('‚ùå DATABASE_URL n√£o est√° configurada!')
  console.error('‚ùå Configure DATABASE_URL no arquivo .env.local ou nas vari√°veis de ambiente')
  throw new Error('DATABASE_URL n√£o configurada. Configure no .env.local ou vari√°veis de ambiente.')
}

// Pool de conex√µes PostgreSQL
const pool = new Pool({
  connectionString: process.env.DATABASE_URL,
  ssl: {
    rejectUnauthorized: false,
  },
  max: 20,
  idleTimeoutMillis: 30000,
  connectionTimeoutMillis: 2000,
})

pool.on('connect', () => {
  console.log('‚úÖ Conectado ao banco de dados Neon')
})

pool.on('error', (err) => {
  console.error('‚ùå Erro no pool de conex√µes:', err)
})

export default pool





